:root {
  --fontFamily: "Nunito", sans-serif;
  --fontFamily2: "Playfair Display", serif;
  --fontFamily3: "Gosha Sans";
  --fontFamily4: "Raleway";
  --mainColor: #4264FB;
  --secondaryColor: #6ba292;
  --optionalColor: #606060;
  --whiteColor: #ffffff;
  --blackColor: #221638;
  --fontSize: 16px;
  --transition: .5s;
}

@font-face {
  font-family: "Gosha Sans";
  src: url("~/assets/fonts/Gosha Sans/GoshaSans-Regular.ttf");
}
@font-face {
  font-family: "Raleway";
  src: url("~/assets/fonts/Raleway-Medium.ttf");
}
@font-face {
  font-family: "Silka";
  src: url("~/assets/fonts/Silka-Roman-Webfont/silka-medium-webfont.ttf");
}
body {
  padding: 0;
  margin: 0;
  font-size: var(--fontSize);
  font-family: var(--fontFamily);
}

img {
  max-width: 100%;
  height: auto;
}

