/*default-btn*/
.default-btn {
  border: none;
  position: relative;
  display: inline-block;
  text-align: center;
  overflow: hidden;
  z-index: 1;
  color: var(--whiteColor);
  background-color: var(--mainColor);
  transition: var(--transition);
  border-radius: 5px;
  font-weight: 700;
  font-size: var(--fontSize);
  padding-left: 55px;
  padding-right: 30px;
  padding-top: 11px;
  padding-bottom: 11px;
}
.default-btn span {
  position: absolute;
  display: block;
  width: 0;
  height: 0;
  border-radius: 50%;
  background-color: var(--blackColor);
  transition: width 0.5s ease-in-out, height 0.5s ease-in-out;
  transform: translate(-50%, -50%);
  z-index: -1;
  border-radius: 5px;
}
.default-btn i {
  position: absolute;
  left: 30px;
  top: 10px;
}
.default-btn:hover {
  color: var(--whiteColor);
}
.default-btn:hover span {
  width: 225%;
  height: 562.5px;
}

.default-btn-style-two {
  border: none;
  position: relative;
  display: inline-block;
  text-align: center;
  overflow: hidden;
  z-index: 1;
  color: var(--whiteColor);
  background-color: transparent;
  transition: var(--transition);
  border-radius: 30px;
  border: 1px dashed var(--mainColor);
  font-weight: 700;
  font-size: var(--fontSize);
  padding-left: 60px;
  padding-right: 35px;
  padding-top: 15px;
  padding-bottom: 15px;
}
.default-btn-style-two i {
  position: absolute;
  left: 35px;
  top: 14px;
}
.default-btn-style-two::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: var(--mainColor);
  z-index: -1;
  border-radius: 30px;
  margin: 4px;
  transition: 0.3s;
}
.default-btn-style-two:hover {
  color: var(--whiteColor);
}
.default-btn-style-two:hover::before {
  margin: 0;
}

/*form-control*/
.form-control {
  height: 50px;
  color: var(--blackColor);
  box-shadow: unset !important;
  border: 1px solid #f5f5f5;
  background-color: #f5f5f5;
  transition: var(--transition);
  border-radius: 3px;
  padding: 1px 0 0 15px;
  font-size: var(--fontSize);
  font-weight: 400;
}
.form-control::placeholder {
  color: #7e7e7e;
  transition: var(--transition);
}
.form-control:focus {
  border-color: var(--mainColor);
  background-color: transparent;
}
.form-control:focus::placeholder {
  color: transparent;
}

textarea.form-control {
  height: auto;
  padding-top: 15px;
}

/*page-title*/
.page-title-area {
